'use strict';

document.addEventListener('DOMContentLoaded', function(){
    
    /* Menú */
    const itemm = document.querySelector('#itemm');
    itemm.addEventListener('click',function(e){e.preventDefault();});

    let menuM = document.querySelector('#menu-movil');
    if(screen.width < 769){
        if(menuM){/* console.log(menuM); */
            let nave = document.querySelector('#nave'); /* console.log(nave); */
            nave.classList.add('menuM');
            menuM.addEventListener('click', function(){
                nave.classList.toggle('menuM');
            });
        }
    }

    /* Carrito Principal*/
    const carritoM = document.getElementById('glider-main');
    if(carritoM){
        const dots = document.querySelector('#dotsM');
        const left = document.querySelector('#glider-prevM');
        const right = document.querySelector('#glider-nextM');
        carrito(carritoM, 1, dots, left, right);
    }

    /* Carrito Catalogo */
    const carritoC = document.querySelector('#glider-catalogo');
    if(carritoC){/* console.log(carritoC); */
        const dots = document.querySelector('#dotsC');
        const left = document.querySelector('#glider-prevC');
        const right = document.querySelector('#glider-nextC');
        carrito(carritoC, 3, dots, left, right);
    }
 


});

function carrito(carrito, item, dots, left, right){
    if(screen.width < 769 || item === 1){
        new Glider(carrito, {
            slidesToShow: 1,
            dots: dots,
            draggable: true,
            rewind: true,
            arrows: {
              prev: left,
              next: right
            },
            easing: function (x, t, b, c, d) {
              return c*(t/=d)*t + b;
            }
          });
    } else if(screen.width >= 769 && item === 1){
        new Glider(carrito, {
            slidesToShow: 1,
            dots: dots,
            draggable: true,
            rewind: true,
            arrows: {
              prev: left,
              next: right
            },
            easing: function (x, t, b, c, d) {
              return c*(t/=d)*t + b;
            }
          });
    } else if(screen.width >= 769 && item === 3){
        new Glider(carrito, {
            slidesToShow: 3,
            dots: dots,
            draggable: true,
            rewind: true,
            arrows: {
              prev: left,
              next: right
            },
            easing: function (x, t, b, c, d) {
              return c*(t/=d)*t + b;
            }
          });
    }
    
}

